.search--btn {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: left;
  margin: 0 auto;
  margin-top: 20px;
}
[id='search-btn'] {
  position: absolute;
  left: -100vw;
}
[id='search-btn'] ~ * {
  --i: 0;
  --j: calc(1 - var(--i));
  transition: 0.65s;
}
[id='search-btn']:checked ~ * {
  --i: 1;
}
[for='search-btn'] {
  overflow: hidden;
  position: relative;
  z-index: 1;
  width: 5em;
  height: 5em;
  border-radius: 50%;
  -webkit-transform: translate(calc(-8.75em * var(--j) )) rotate(45deg);
          transform: translate(calc(-8.75em * var(--j) )) rotate(45deg);
  box-shadow: 0 0 1.5em rgba(0, 0, 0, .4);
  background: #d9eb52;
  text-indent: -100vw;
  cursor: pointer;
}
[for='search-btn']:before, [for='search-btn']:after {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -0.8125em;
  width: 1.625em;
  height: 1.625em;
  transition: inherit;
  content: '';
}
[for='search-btn']:before {
  margin-top: -0.08125em;
  height: 0.203125em;
  -webkit-transform-origin: 100% 50%;
          transform-origin: 100% 50%;
  -webkit-transform: translate(calc(0.40625em * var(--j))) scalex(calc(1 - .5 * var(--j)));
          transform: translate(calc(0.40625em * var(--j))) scalex(calc(1 - .5 * var(--j)));
  background: currentcolor;
}
[for='search-btn']:after {
  border-radius: calc(50% * var(--j));
  -webkit-transform: translate(calc(-0.40625em * var(--j))) scalex(calc(var(--j) + 0.125 * var(--i)));
          transform: translate(calc(-0.40625em * var(--j))) scalex(calc(var(--j) + 0.125 * var(--i)));
  box-shadow: inset 0 0 0 0.203125em currentcolor;
}
[id='search-bar'] {
  border: none;
  margin-right: -2.5em;
  padding: 0 calc(2.5em + 1em) 0 1em;
  width: 20em;
  height: 3.25em;
  border-radius: 1.625em 0 0 1.625em;
  -webkit-transform: translate(calc(8.75em * var(--j)));
          transform: translate(calc(8.75em * var(--j)));
  background: #3f324d;
  color: #fff;
  font: 1em century gothic, verdana, arial, sans-serif;
  -webkit-clip-path: inset(-3em calc(17.5em * var(--j)) -3em -3em);
          clip-path: inset(-3em calc(17.5em * var(--j)) -3em -3em);
}
[id='search-bar']::-webkit-input-placeholder {
  opacity: 0.5;
  color: inherit;
  font-size: 0.875em;
  letter-spacing: 1px;
  text-shadow: 0 0 1px, 0 0 2px;
}
[id='search-bar']:-ms-input-placeholder {
  opacity: 0.5;
  color: inherit;
  font-size: 0.875em;
  letter-spacing: 1px;
  text-shadow: 0 0 1px, 0 0 2px;
}
[id='search-bar']::placeholder {
  opacity: 0.5;
  color: inherit;
  font-size: 0.875em;
  letter-spacing: 1px;
  text-shadow: 0 0 1px, 0 0 2px;
}
[id='search-bar']:focus {
  outline: none;
  box-shadow: 0 0 1.5em #2a2a2a, 0 0.75em 1.5em rgba(0, 0, 0, .2);
  background: #8458ff;
  color: #000;
}

